import React, { useState, useEffect } from 'react'
import { Link, useParams, useNavigate   } from 'react-router-dom'
import { Row, Col, Image, ListGroup, Button, Card, Form, ListGroupItem} from 'react-bootstrap'
import Rating from '../Rating'
import { useDispatch, useSelector} from 'react-redux'
import {listProductDetails, createProductReview} from '../../actions/productActions'
import { PRODUCT_CREATE_REVIEW_RESET } from '../../constant/productConstant'
import Loader  from '../Loader'
import Message  from '../Message'

function ProducScreen() {
    const dispatch = useDispatch()

    const productDetails = useSelector(state => state.productDetails)
    const {loading, error, product} = productDetails

    const userLogin = useSelector(state => state.userLogin)
    const {userInfo} = userLogin

    const productReviewCreate = useSelector(state => state.productReviewCreate)
    const {loadin: loadingProductReview, error:errorProductReview, success:successProductReview} = productReviewCreate

    const {id} = useParams()
    let navigate = useNavigate();

    const [qty, setQty] = useState(1)
    const [rating, setRating] = useState(0)
    const [comment, setComment] = useState('')

  useEffect(() => {
      if(successProductReview){
            setRating(0)
            setComment('')
            dispatch({ type: PRODUCT_CREATE_REVIEW_RESET })
      }
       dispatch(listProductDetails(id))
  }, [dispatch, successProductReview])

  const addToCardHandler = () =>{
      navigate(`/cart/${id}?qty=${qty}`)
  }

  const submitHundler = (e) =>{
    e.preventDefault()
    dispatch(createProductReview(
        id,
        {
            rating,
            comment
        }
    ))
  }
  return (
    <div>
        <Link to="/" className="btn btn-light my-3">Go Back</Link>

        {
            loading ? 
                <Loader />
                : error
                    ? <Message variant='danger'>{error}</Message>
                : (
                    <div>
                    <Row>
                        <Col md={6}>
                            <Image src={product.image} alt={product.name} fluid />
                        </Col>

                        <Col md={3}>
                            <ListGroup variant='flush'>
                            <ListGroup.Item>
                                <h3>{product.name}</h3>
                            </ListGroup.Item>

                            <ListGroup.Item>
                                <Rating value={product.rating} text = {`${product.numReviews} reviews`} color={'#f8e825'} />
                            </ListGroup.Item>

                            <ListGroup.Item>
                                Price : ${product.price}
                            </ListGroup.Item>

                            <ListGroup.Item>
                                Description : {product.description}
                            </ListGroup.Item>

                            </ListGroup>
                        </Col>
                        <Col md={3}>
                        <Card>
                                <ListGroup variant='flush'>
                                    <ListGroup.Item>
                                        <Row>
                                            <Col>Price:</Col>
                                            <Col>
                                                <strong>${product.price}</strong>
                                            </Col>
                                        </Row>
                                    </ListGroup.Item>
                                    <ListGroup.Item>
                                        <Row>
                                            <Col>Status:</Col>
                                            <Col>
                                                {product.countInStock > 0 ? 'In Stock' : 'Out of Stock'}
                                            </Col>
                                        </Row>
                                    </ListGroup.Item>
                                    {product.countInStock > 0 && (
                                        <ListGroup.Item>
                                            <Row>
                                                <Col> Qty: </Col>
                                                <Col xs='auto' className='my-1'>
                                                        <Form.Control as='select' value={qty} onChange={(e) => setQty(e.target.value)}>
                                                            {[...Array(product.countInStock).keys()].map((x)=>(
                                                                <option value={x+1} key={x+1}>
                                                                    {x+1}
                                                                </option>
                                                            ))}
                                                        </Form.Control>
                                                </Col>
                                            </Row>
                                        </ListGroup.Item>
                                    )}
                                    <ListGroup.Item>
                                        <Row>
                                        
                                            <Button className='btn-block' onClick = {addToCardHandler} disabled={product.countInStock <= 0 } type='button'>Add to Cart</Button>
                                
                                        </Row>
                                    </ListGroup.Item>
                                </ListGroup>
                            </Card>    
                        </Col>
                    </Row>

                    <Row className='mt-3'>
                        <Col md={6}>
                            <h4>Review</h4>

                            {product.reviews.length === 0 && <Message variant='info'>No Reviews</Message>}

                            <ListGroup variant='flush'>
                                {product.reviews.map((review) =>(
                                    <ListGroup.Item key={review._id}>
                                        <div style={{display: 'flex', justifyContent: "space-between"}}>
                                            <div style={{display: 'flex', gap:'1rem'}}>
                                                <strong style={{fontSize: '1.1rem',fontWeight: "900"}}>{review.name}</strong>
                                                <Rating value={review.rating} color='#f8e825' />
                                            </div>
                                            <p style={{fontSize: '12px'}}>{review.createdAt.substring(0,10)}</p>
                                        </div>
                                        <p style={{margin: '0px 8px', padding:'1rem', background: '#3f4244', color:'#f6f4f4', borderRadius:'12px', fontSize:'13px'}}>{review.comment}</p>
                                    </ListGroup.Item>
                                ))}

                                <ListGroup.Item>
                                    <h4>Write a review</h4>
                                    {loadingProductReview && <Loader />}
                                    {successProductReview && <Message variant='success'>Review Submitted</Message>}
                                    {errorProductReview && <Message variant='danger'>{errorProductReview}</Message>}
                                    {userInfo ? (
                                        <Form onSubmit={submitHundler}>
                                            <Form.Group controlId='rating'>
                                                <Form.Label>Rating:</Form.Label>
                                                <Form.Control as='select' value={rating} onChange={(e) => setRating(e.target.value)}>
                                                        <option value=''>Select...</option>
                                                        <option value='1'>1 - Poor</option>
                                                        <option value='2'>2 - Fair</option>
                                                        <option value='3'>3 - Good</option>
                                                        <option value='4'>4 - Very Good</option>
                                                        <option value='5'>5 - Excellent</option>
                                                </Form.Control>
                                            </Form.Group>

                                            <Form.Group controlId='comment'>
                                                <Form.Label>Review</Form.Label>
                                                <Form.Control as='textarea' row='5' value = {comment} onChange={(e) => setComment(e.target.value)} placeholder='...'>

                                                </Form.Control>
                                            </Form.Group>
                                            <Button disabled = {loadingProductReview} type='submit' variant='primary' className='w-100 mt-3'>Submit</Button>
                                        </Form>
                                    ) : (
                                        <Message variant='info'>Please<Link to='/login'>login</Link>to write a review</Message>
                                    )}
                                </ListGroup.Item>
                            </ListGroup>
                        </Col>
                    </Row>
                    </div>
                )
        }
        
    </div>
  )
}

export default ProducScreen